import React from 'react'
import Drawer from './Drawer'
import { HamburgerElastic } from 'react-animated-burgers'
import ninja from '../img/ninja.png'
import ninja2 from '../img/ninja2.png'

const navbarItems = ["Home", "YouTube", "Contact"]

export default function Navbar(props) {


    const navbarItemList = navbarItems.map((navbarItem, index) => {
        return (
                <div style={{fontSize: 16}} key={index} onClick={()=> {props.onClick(navbarItem)}} className="link">
                    {navbarItem}</div>
        )
    })

    const desktopMenu = (
        
                            <div style={{height: '10%'}} className="navbar" >
                            <img className="navbar-icon" src={ninja} alt="home" height='50px' width="200px" />
                            {navbarItemList}
                            </div>
                         )

    const mobileMenu = ( <div className="menu-icon-container" >
                            <img className="navbar-icon" src={ninja2} alt="home" height='50px' width="50px" />
                            <HamburgerElastic isActive={props.isActive} toggleButton={props.openDrawer} />
                            <Drawer onClick={props.onClick}   drawerWidth={props.drawerWidth} />

                    </div>

                )
    
    return ( props.mobile ? mobileMenu : desktopMenu)

}




