import React from 'react'
const menuItems = ["Home", "YouTube", "Contact"]
export default function Drawer(props) {
    let drawerWidth = null

    const menuItemList = menuItems.map((menuItem, index) => {
        return (
                <li  key={index} className="drawer-link"><a style={{color: '#ebebeb'}}  href={`#${menuItem}`}>{menuItem}</a></li>
        )
    })

    return(
            <div className="drawer" style={{width: props.drawerWidth}}>
                <ul>
                    {menuItemList}
                </ul>
            </div>
    )
}