import React, { Component } from 'react'
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';

export default class ContactForm extends Component {
    render() {
        return(

            <Formik
            
                initialValues={{ name: '', email: '', comments: '' }}
                validationSchema={Yup.object({
                    name: Yup.string()
                    .max(35, 'Must be 35 characters or less.')
                    .required('Please enter your name.'),
                    email: Yup.string()
                    .email('Invalid email address.')
                    .required("Please enter your email address."),
                    comments: Yup.string()
                    .max(850, "Must be 850 characters or less.")
                    .required("Please enter your comments.")
                })}
                onSubmit={(values) => {
                    alert("Thank you for your feedback.")
                }}
            >
                <Form className="contact-form" >
                    <div>
                    <h2 style={{ fontSize: '150%'}}>Contact Form</h2> 
                    </div>
                    <div>
                    <p style={{fontSize: '100%'}}> Comments? Found a software bug?   </p>
                    <p style={{fontSize: '100%'}}> Please let us know.  Your feedback is greatly appreciated. </p>
                    </div>
                    <label style={{fontSize: '120%'}} className="form-label" htmlFor="name">Name:
                                         <ErrorMessage render={msg => <span style={{fontSize: '70%'}} className="error-message">{msg}</span>}  name="name" /></label>

                    <Field style={{fontSize: '100%'}} className="inputs" name="name" type="text" />
                    <label style={{fontSize: '120%'}}  className="form-label" htmlFor="email">Email:
                                        <ErrorMessage  render={msg => <span style={{fontSize: '70%'}} className="error-message">{msg}</span>} name="email" /></label>
                    <Field style={{fontSize: '100%'}} className="inputs" name="email" type="email" />

                    <label style={{fontSize: '120%'}}  className="form-label" htmlFor="comments">Comments:                                                  <ErrorMessage render={msg => <span  
                                          style={{fontSize: '70%'}} className="error-message">{msg}</span>} name="comments" /></label>
                    <Field style={{fontSize: '100%'}} component="textarea" 
                    className="comments-input" name="comments" type="text" />
                    <div style={{height: '40px' , width: '170px'}}>
 
                    <button className="submit-button" type="submit">Submit</button>
                    </div>

                </Form>
            </Formik>
        )
    }
}