import React, { Component } from 'react';
import Navbar from '../shared/layout/Navbar'
import Content from '../shared/layout/Content'
import Footer from '../shared/layout/Footer'
import Home from './Home'
import YouTube from './YouTube'
import Contact from './Contact'


import '../index.css'


export default class App extends Component {
  constructor(props){
    super(props)

    this.state= {type: 'Home',
                mobile: window.innerWidth >= 768 ? false : true,
                drawerWidth: null,
                marginLeft: 0,
                width: null,
                isActive: false}
    
    window.addEventListener('resize', this.handleResize)
    
   
  }

componentDidMount() {
  this.handleResize()
  this.content = this.SlideContent(this.state.type)
  
}

componentWillUnmount() {
  window.removeEventListener('resize', this.handleResize)
}

 SlideContent = (type) => {
  switch(type) {
    case 'Home':
      return <Home class="home "/>
    case 'YouTube':
      return <YouTube />
    case 'Contact':
      return <Contact />
    default: 
     return <Home class="home "/>
  }
}
  
handleNavbarPress = (type) => {
  this.setState({type: type}) 
}

handleResize = () => {
 
  if(window.innerWidth > 768 && this.state.mobile === true) {
    this.setState({mobile: false})
  }
  if(window.innerWidth <= 768 && this.state.mobile === false) {
   this.setState({mobile: true})
  }
}

openDrawer = () => {
  this.state.isActive ? this.setState({ width: null, drawerWidth: 0, isActive: false, marginLeft: 0}) : this.setState({width: '150px', drawerWidth: '150px', isActive: true, marginLeft: '-150px' })

}
closeDrawer = () => {
 
}

render() {

  this.content = this.SlideContent(this.state.type)

   const desktopVersion = (
           <div style={{height: '100%'}}>
           <Navbar mobile={this.state.mobile}  onClick={this.handleNavbarPress}/>
           <Content>
           {this.content}
           </Content>
           <Footer />
         </div> 
    )
  
    const mobileVersion = (
      <div className="container" >
      
      <Navbar mobile={this.state.mobile} openDrawer={this.openDrawer} closeDrawer={this.closeDrawer} isActive={this.state.isActive} drawerWidth={this.state.drawerWidth} onClick={this.handleNavbarPress}/>
      <Content style={{ marginLeft: this.state.marginLeft, marginRight: this.state.drawerWidth }}>
        <div id="Home"><Home class="home"/></div>
        <div id="YouTube"><YouTube /></div>
        <div id="Contact"><Contact /></div>
        
      </Content>
      <Footer />

      
    </div> 
)

    return ( this.state.mobile ? mobileVersion: desktopVersion
    )
  }
}


